body {
    margin: 0;
    padding: 0;
    font-family: "Courier New", Courier, monospace;
    background: -webkit-linear-gradient(
        left,
        rgb(235, 244, 225),
        rgb(255, 235, 192)
    );
    background: linear-gradient(
        to right,
        rgb(235, 244, 225),
        rgb(255, 235, 192)
    );
}

button {
  cursor: pointer;
  background-color: #5f2e00;
}
.Tilt {
    background: -webkit-linear-gradient(
        left,
        rgb(235, 244, 225),
        rgb(255, 235, 192)
    );
    background: linear-gradient(
        to right,
        rgb(235, 244, 225),
        rgb(255, 235, 192)
    );
}

.form {
    width: 700px;
    background: -webkit-radial-gradient(
                0% 50%,
                circle farthest-side,
                #fb1 23.5%,
                rgba(240, 166, 17, 0) 0
            )
            21px 30px,
        -webkit-radial-gradient(
                0% 50%,
                circle farthest-side,
                #b71 24%,
                rgba(240, 166, 17, 0) 0
            )
            19px 30px,
        -webkit-linear-gradient(
                #fb1 14%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 85%,
                #fb1 0
            )
            0 0,
        -webkit-linear-gradient(
                300deg,
                #fb1 24%,
                #b71 0,
                #b71 26%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 74%,
                #b71 0,
                #b71 76%,
                #fb1 0
            )
            0 0,
        -webkit-linear-gradient(
                60deg,
                #fb1 24%,
                #b71 0,
                #b71 26%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 74%,
                #b71 0,
                #b71 76%,
                #fb1 0
            )
            0 0,
        -webkit-linear-gradient(left, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
    background: radial-gradient(
                circle farthest-side at 0% 50%,
                #fb1 23.5%,
                rgba(240, 166, 17, 0) 0
            )
            21px 30px,
        radial-gradient(
                circle farthest-side at 0% 50%,
                #b71 24%,
                rgba(240, 166, 17, 0) 0
            )
            19px 30px,
        linear-gradient(
                #fb1 14%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 85%,
                #fb1 0
            )
            0 0,
        linear-gradient(
                150deg,
                #fb1 24%,
                #b71 0,
                #b71 26%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 74%,
                #b71 0,
                #b71 76%,
                #fb1 0
            )
            0 0,
        linear-gradient(
                30deg,
                #fb1 24%,
                #b71 0,
                #b71 26%,
                rgba(240, 166, 17, 0) 0,
                rgba(240, 166, 17, 0) 74%,
                #b71 0,
                #b71 76%,
                #fb1 0
            )
            0 0,
        linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
    background-size: 40px 60px;
}

.bounding-box {
    position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}

.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
